import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';

const proxyurl = "https://cors-anywhere.herokuapp.com/";
const api_key_bit = 'fef11179-8dac-483a-824d-38ec1ad205d9';
const api_key_not = 'f14bd22cd4804fe99ef2b44b07c3d3a6';
const crypto = 1; //Bitcoin ID
const moneda = 'PEN' // Moneda Peru
const biturl = `https://pro-api.coinmarketcap.com/v1/cryptocurrency/quotes/latest?CMC_PRO_API_KEY=${api_key_bit}&id=${crypto}&convert=${moneda}`;
const noticiasurl = `https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=${api_key_not}&language=es`;

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio={props.precioBitcoin}
                />
            </div>
            <div className="col-md-8">
                <h2>Noticias sobre Bitcoin</h2>
                <Noticias 
                    noticias={props.noticias}
                />
            </div>
            <div className="col-md-4">
                <h2>Proximos eventos Bitcoin</h2>
            </div>
        </div>
    </MasterPage>
    
);
Index.getInitialProps = async () => {
    const precio = await fetch(biturl);
    const noticias = await fetch(noticiasurl);
    
    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();

    return {
        precioBitcoin: resPrecio.data[crypto].quote[moneda],
        noticias: resNoticias.articles
    }
}

export default Index;